<template>
    <div class="marketing-wrapper">
        <div>
             {{ fpId }}
        </div>
        <div>
             {{ fpIdOld }}
        </div>
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px">
            <el-form-item>
                <!-- <el-button type="primary" @click="getFpId()"></el-button> -->
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import fingerprint from "@/assets/js/fp.js";
import FPOJS from "@/assets/js/fpold.js";
export default {
    data() {
        var validatePhone = (rule, value, callback) => {
            const regex = /^1(3[0-9]|4[5-9]|5[0-3,5-9]|6[5,6]|7[2-35-8]|8[0-9]|9[1,8,9])\d{8}$/;
            if (value === '') {
                callback(new Error(''));
            } else {
                if (!regex.test(value)) {
                    callback(new Error('!'));
                }
                callback();
            }
        };
        return {
            ruleForm: {
                phone: '',
            },
            rules: {
                phone: [
                    // { required: true, trigger: 'blur', message: '' },
                    // { validator: validatePhone, trigger: 'blur' }
                ],
            },
            fpId: '',
            fpIdOld: ''
        };
    },
    methods: {
        submitForm(formName) {
            this.axios.post("https://lm.virbox.com/index/ocpcSendBack", {
                conversionTypes: [
                    {
                        logidUrl: "http://localhost:9001/?bd_vid=123456",
                        newType: 25,
                    }
                ]
            }).then((res) => { });
        },
        //
        getFpId() {
            new Promise((resolve, reject) => {
                fingerprint.load().then((fp => fp.get()))
                    .then((result) => {
                        this.fpId = result.visitorId
                    })
            })
            new Promise((resolve, reject) => {
                FPOJS.load().then((fp => fp.get()))
                    .then((result) => {
                        this.fpIdOld = result.visitorId
                    })
            })
        }
    },
    mounted() {
        this.getFpId()
    }
}
</script>
<style lang="scss">
.marketing-wrapper {
    width: 500px;
}
</style>